            timeline: null,
            wh: document.documentElement.clientHeight,

            c_uspbottomHeight: 0,
            c_frombottomscroll: 0,
            c_bottomWndOffset: 80,
            c_topWndOffset: 55,

            p_b: false,
            p_f: false,

            updateTimeout: null,

            events: [],

            firstPaginate: true

