<template>
  <div class="page chat">
    <topheader class="topheader" :u="u" :chat="chat" @closeAboutUser="closeAbout" @showAboutRoom="getClicked" :aboutUser="aboutShow" :roomInfo="roomInfo" @closeRoom="closingRoom"/>
    <div 
      class='headerSpacer'
      :class="pocketnet ? 'pocketnet' : ''"
      v-if="!roomInfo"
    >
      <chat @sending="sending" @newchat="newchat" :u="u" :chat="chat" @getEvents="eventsRoom"/>
    </div>
    <about :chat="m_chat" :events="events" @showAbout="aboutUser" @closeAboutRoom="roomInfoClose" v-else/>
    <contact v-if="aboutShow" :contact="aboutUserInfo" class="aboutContact"/>
  </div>
</template>

<style scoped lang="sass">

.topheader
  width: 100%
  top: 0
  z-index: 999

.aboutContact
  position: absolute
  left: 0
  right: 0
  top: 60px
  bottom: 0
  z-index: 999
  background: $color-applebuttontext
</style>

<script>

import chat from '@/components/chat/index.vue';
import topheader from '@/components/chat/topheader/index.vue';
import about from '@/components/chat/about/index.vue';
import contact from '@/components/contact/index.vue';
import { mapState } from 'vuex';
import * as _ from "underscore";

export default {
  name: 'pagechat',
  components: {
    contact,
    chat,
    topheader,
    about
  },
  data: () => {
    return {
      roomInfo: false,
      m_chat: {},
      events: Array,
      aboutShow: false,
      aboutClose: false,
      aboutUserInfo: {},
    }
  },

  computed: {
    u() {
      return this.$route.query.u
    },
    chat() {
      var id = this.$route.query.id
      return this.$store.state.chatsMap[id]
    },

    ...mapState({
      pocketnet: state => state.pocketnet,
    }),

  },
  mounted() {
    
  },
  methods: {
    aboutUser(user){
      this.aboutUserInfo = user
      this.aboutShow = true
    },
    newchat(_chat) {

    },
    getClicked(room) {
      console.log(room, "clicked room here from chat page")
      this.roomInfo = true
      console.log(this.$route)
      this.m_chat = room

      // console.log('this.m_chat', this.m_chat)
    },
    sending() {
      console.log("SENDING")
    },
    closingRoom(val) {
      this.roomInfo = val
    },
     eventsRoom(data){
      // console.log('event room', data)
      this.events = data
    },
    closeAbout(value) {
      this.aboutShow = value
    },
    roomInfoClose(value){
      this.roomInfo = value
    }

  },


}
</script>
